"use client";
import React from "react";
import Diagramme from "../Diagramme";
import Heading from "../Heading";
import { useState } from "react";
import styles from "@/app/css/pages/_loginPageContent.module.scss";
import Button from "../Button";
import Input from "../Input";
import InputField from "../global/InputField";
import Label from "../global/Label";
const LoginPageContent = () => {
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  async function handleSubmit(e) {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const res = await fetch("http://localhost/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });

      if (!res.ok) {
        const err = await res.json();
        throw new Error(err.message || "Erreur connexion");
      }

      const data = await res.json();

      // Utiliser la fonction login pour mettre Ã  jour le contexte + localStorage
      login(data.user); // Assure-toi que data.user contient les infos utilisateur

      // Si tu veux aussi stocker un token, tu peux faire ici :
      localStorage.setItem("token", data.token);
    } catch (e) {
      setError(e.message);
    } finally {
      setLoading(false);
    }
  }

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleLogin = async () => {
    console.log(formData);
  };

  return (
    <section className={styles.loginPageContent}>
      <Heading title={`Connexion Ã  TrackMyGrades`} textAlign={"center"} />
      <form className={styles.formContainer}>
        <InputField>
          <Label text={"Adresse e-mail"} name={"email"} />
          <Input
            id="email"
            type="email"
            name="email"
            placeholder="Email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </InputField>
        <InputField>
          <Label text={"Mot de passe"} name={"password"} />
          <Input
            id="password"
            type="password"
            name="password"
            placeholder="Mot de passe"
            value={formData.password}
            onChange={handleChange}
            required
          />
        </InputField>
        <Button text={"Se connecter"} style={"theme"} margin={"0 auto"} />
      </form>

      {error && <p style={{ color: "red" }}>{error}</p>}
    </section>
  );
};

export default LoginPageContent;
